.wrapper{
    background-color: black;
}
.content{
    max-width: 1200px;
    width: 100%;
    margin: 0 auto;
    padding: 120px 15px;
}
.block{
}
.block__title p{
    color: white;
    font-weight: 800;
    font-size: 24px;
    text-align: center;
}
.btn {
    display: block;
    padding: 0 20px;
    margin: 0 auto;
    outline: none;
    border: none;
    border-radius: 6px;
    height: 40px;
    line-height: 40px;
    text-decoration: none;
    color: #1a1a1a;
    font-weight: 500;
    font-size: 18px;
    cursor: pointer;
    user-select: none;
    appearance: none;
    touch-action: manipulation;
}
.btn:focus-visible {
    box-shadow: 0 0 0 2px #666;
}
.btn:hover {
    background-color: #DBE7F2;
}
.btn:active {
    transform: scale(0.96);
}
.btn:disabled {
    pointer-events: none;
    opacity: 0.65;
}